.try-again-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.try-again-container > h1 {
    text-decoration: underline;
    margin: 0;
    font-size: 36px;
    margin-bottom: 20px;
    
}

.result-container{
    font-size: 20px;
    margin: 7px;
}

.btn{
    position: relative;
    margin: 12px;
    border: none;
    border-radius: 4px;
    min-width: 64px;
    padding: 2px 16px;
    vertical-align: middle;
    text-align: center;
    color: #fff;
    box-shadow: 0 1px 3px rgba(0, 0, 0, 1.2), 0 1px 2px rgba(0, 0, 0, 0.26);
    font-size: 18px;
    font-weight: 500;
    line-height: 36px;
    cursor: pointer;
    transition: background-color 0.2s;
}

.retry{
    background-color: #3e4248;
}

.retry:hover, .retry:focus{
    background: #808080;
}

.fb-share{
    background: #3b5998;
}

.fb-share:hover, .fb-share:focus{
    background: #5d7dc4;
}


.tweet{
    background: #1da1f2;
}

.tweet:hover, .tweet:focus{
    background: #8faef3;
}

